<template>
    <div class="user-list">
        <SideBar />
        <div class="iframe">
            <HeaderManagement />
            <BodyManagement />

            <div class="title-section">
                <h2 class="heading-section">Tutorials List</h2>
                <router-link to="/add-tutorial" class="add-btn">
                    <i class="fa-solid fa-plus"></i>
                </router-link>
            </div>

            <ul class="list-group">
                <li class="list-group-item">
                    <div class="flex-section">
                        <div class="actions">
                            <span class="semi-bold">Actions</span>
                        </div>
                        <div class="email-address">
                            <span class="semi-bold">Tutorial Title</span>
                        </div>
                    </div>
                </li>
            </ul>
    
            <ul class="list-group">
                <li
                    class="
                    list-group-item
                    d-flex
                    justify-content-between
                    align-items-center"
                    v-for="title in titles"
                    :key="title.id"
                >
                    <div class="flex-section">
                        <div class="actions">
                            <router-link
                                :to="{ path: '/edit-tutorial/'+title.id }"
                                class="btn btn-primary"
                            >
                                <i class="fa-solid fa-user-pen"></i>
                            </router-link>
                            <a href="#" class="btn btn-danger mx-1" @click="deleteUser(title.id)">
                                <i class="fa-solid fa-user-minus"></i>
                            </a>
                        </div>
                        <div class="email-address">
                            {{ title.title }}
                        </div>
                    </div>
                </li>
            </ul>
            
        </div>
    </div>
</template>

<script>
import SideBar from "../components/SideBar.vue";
import HeaderManagement from "../components/HeaderManagement.vue";
import BodyManagement from "@/components/BodyManagement.vue";

import { initializeApp } from 'firebase/app'
import { getFirestore, collection } from 'firebase/firestore'

const firebaseConfig = {
    apiKey: "AIzaSyBMHJcISZ5OP_U1gdGD0MEFpqC4aHAy7T4",
    authDomain: "portfolio-65454.firebaseapp.com",
    projectId: "portfolio-65454",
    storageBucket: "portfolio-65454.appspot.com",
    messagingSenderId: "330355731809",
    appId: "1:330355731809:web:97dc40d4f54b9bfa392920"
}

const app = initializeApp(firebaseConfig)
const db = getFirestore(app)

const tutorialsColRef = collection(db, 'tutorials')
import { getDocs, doc, deleteDoc } from 'firebase/firestore'

export default {
    name: 'UsersListView',
    components: {
    SideBar,
    HeaderManagement,
    BodyManagement
    },
    data() {
        return {
            titles: [],
            selectedDoc: null
        }
    },
    methods: {
        async fetchTutorials () {
            const tutorialsSnapShot = await getDocs(tutorialsColRef)
            const titles = []
            tutorialsSnapShot.forEach((title) => {
                const tutorialData = title.data()
                tutorialData.id = title.id
                titles.push(tutorialData)
            })
            this.titles = titles
        },
        async deleteUser (id) {
            const titleRef = doc(tutorialsColRef, id)
            await deleteDoc(titleRef)
            alert('Email address deleted successfully!')
            this.$router.go()
        }
    },
    created () {
        this.fetchTutorials()
    }
}
</script>

